<template>
  <div id="tablet">
    <div class="queryBox">
      <span class="title"><i class="iconfont icon-shenhe1"></i>人类活动变化检测详情</span>
      <Button type="primary" size="small" icon="ios-arrow-back" class="sign-btn" @click="$emit('signOut',false)">返回</Button>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="detailsLoading" :columns="detailsColumns1" :data="detailsData1" highlight-row :max-height="tableHeight">
        </Table>
        <div class="page_box">
          <Page :total="detailsTotal" ref="detailsPages" :page-size="detailsPageSize" :current.sync="detailsPageNum" @on-change="detailsPageChange" show-total/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  // 表格列表
  list
} from '@/api/projectApproval'
export default {
  name: 'detailsTable',
  data () {
    return {
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 查询框查询条件 */
      queryData: {},
      /* 表格数据 */
      detailsColumns1: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.detailsPageNum - 1) * this.detailsPageSize + 1)
          }
        },
        {
          title: '任务名称',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'cl002'
        },
        {
          title: '原人类活动',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'cl010'
        },
        {
          title: '比对人类活动',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'cl009'
        }
      ],
      detailsData1: [],
      /* 总条数 */
      detailsTotal: 0,
      detailsPageNum: 1,
      detailsPageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      detailsLoading: false
    }
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.detailsPageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
    }, false)
  },
  methods: {
    /* 获取字段列表 */
    getTableList () {
      this.detailsLoading = true
      list({
        data: {
          pageSize: this.detailsPageSize,
          pageNumber: this.detailsPageNum
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.detailsData1 = res.data.data.rows
          this.detailsTotal = res.data.data.total
          this.detailsLoading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    detailsPageChange (pageNum) {
      this.detailsPageNum = pageNum
      this.getTableList()
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    height: 100%;
    overflow: hidden;
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      line-height: 50px;
      text-align: right;
      .title {
        float: left;
        font-size: 14px;
        i {
          vertical-align: middle;
          font-size: 20px;
          margin-right: 5px;
        }
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
  }
</style>
