<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" ref="getQuery"  style="width: calc(100% - 70px)"></QueryBox>
      <div class="queryBtns">
        <Button type="success" size="small" style="margin-right: 10px" class="btn-add" icon="md-add" @click.stop="openAddModal">新建</Button>
      </div>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight">
        </Table>
        <div class="page_box">
          <Page :total="total" ref="pages" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
    <!-- 查看地图-->
    <div class="changeDetectTableMap" :class="{addActive:addActive}">
      <changeDetectTableMap ref="changeDetectTableMap" :seeId="taskId" @signOut="signOut"></changeDetectTableMap>
    </div>
    <Modal v-model="modal1"
           title="新增人类活动信息变化检测"
           :mask-closable="false"
           >
      <Form :model="addForm" :label-width="130" ref="addForm" :rules="addFormRules">
        <FormItem prop="name" label="任务名称">
          <Input v-model="addForm.name" placeholder="请填写任务名称，最少四个字符"></Input>
        </FormItem>
        <FormItem prop="image1Id" label="现状数据">
          <i-select v-model.trim="addForm.image1Id" style="width: 100%" placeholder="请选择原人类活动数据">
            <i-option v-for="item in activitiesList1" :value="item.id">{{ item.name }}</i-option>
          </i-select>
        </FormItem>
        <FormItem prop="image2Id" label="本底数据">
          <i-select v-model.trim="addForm.image2Id" style="width: 100%" placeholder="请选择比对人类活动数据">
            <i-option v-for="item in activitiesList2" :value="item.id">{{ item.name }}</i-option>
          </i-select>
        </FormItem>
        <FormItem prop="remark" label="说明">
          <Input v-model="addForm.remark" type="textarea"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button @click="modal1=false">取消</Button>
        <Button type="primary" @click="submitAddForm">确认</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import changeDetectTableMap from './changeDetectTable_map'
import { renderTime } from '@/utils/common'
import { getChangeList, getRemoteList2, addTask, getActivitiesList2, getActivityChangeList, delActivityChangeRask } from '@/api/remoteSensing'
export default {
  name: 'changeDetectMap',
  data () {
    return {
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 查询框查询条件 */
      queryData: {},
      /* 表格数据 */
      columns1: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '任务名称',
          align: 'center',
          ellipsis: true,
          minWidth: 200,
          key: 'name'
        },
        {
          title: '对比时间',
          align: 'center',
          ellipsis: true,
          minWidth: 140,
          render: (h, params) => {
            return h('span', renderTime(params.row.date))
          }
        },
        {
          title: '描述',
          align: 'center',
          ellipsis: true,
          minWidth: 200,
          key: 'remark'
        },
        {
          title: '现状数据',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'image1'
        },
        {
          title: '本底数据',
          align: 'center',
          ellipsis: true,
          minWidth: 200,
          key: 'image2'
        },
        {
          title: '操作',
          width: 250,
          key: '',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              // 重新计算
              h('Button',
                {
                  props: {
                    type: 'primary',
                    size: 'small',
                    icon: 'ios-aperture'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.newcom(params)
                    }
                  }
                }, '重新计算'),
              // 查看地图
              h('Button',
                {
                  props: {
                    type: 'success',
                    size: 'small',
                    icon: 'ios-map'
                  },
                  style: {
                    marginRight: '5px',
                    background: 'rgb(16,186,204)',
                    borderColor: 'rgb(16,186,204)'
                  },
                  on: {
                    click: () => {
                      this.addActive = true
                      this.$refs.changeDetectTableMap.init(params.row.id)
                    }
                  }
                }, '查看'),
              h('Button',
                {
                  props: {
                    type: 'error',
                    size: 'small',
                    icon: 'md-trash'
                  },
                  on: {
                    click: () => {
                      this.$Modal.confirm({
                        title: '确认',
                        content: '请问您确定要删除该人类活动变化比对任务？',
                        onOk: () => {
                          this.proDelete(params.row.id)
                        },
                        onCancel: () => {
                        }
                      })
                    }
                  }
                },
                '删除'
              )
            ])
          }
        }
      ],
      data1: [],
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      loading: false,
      /* 查看详情弹框状态 */
      addActive: false,
      /* 查看详情任务id */
      taskId: 0,
      /* 控制新增弹框的变量 */
      modal1: false,
      activitiesList1: [],
      activitiesList2: [],
      /* 添加新的人类活动变化检测任务的表单 */
      addForm: {
        name: '',
        image1Id: '',
        image2Id: '',
        remark: ''
      },
      // 添加新的人类活动变化检测任务--数据表单校验
      addFormRules: {
        name: [
          { required: true, message: '任务名称必须填写', trigger: 'blur' }
        ],
        image1Id: [
          { required: true, message: '原人类活动数据必须选择', trigger: 'change', type: 'number' }
        ],
        image2Id: [
          { required: true, message: '比对人类活动数据必须选择', trigger: 'change', type: 'number' }
        ]
      }
    }
  },
  components: {
    QueryBox,
    // 查看详情
    changeDetectTableMap
  },
  computed: {
    // 查询表单参数
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'name',
          title: '任务名称'
        }
      ]
    }
  },
  created () {
    this.getTableList()
    // this.getRemoteList()
    this.getActivities2()
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableList()
    }, false)
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /* 新增----获取比对人类活动数据列表 */
    getActivities2 () {
      getActivitiesList2({
        data: {}
      }).then(res => {
        if (res.data.code === '0000') {
          this.activitiesList1 = res.data.data
          this.activitiesList2 = res.data.data
        }
      })
    },
    /* 获取字段列表 */
    getTableList () {
      let name
      if (this.queryData.name === undefined) {
        name = ''
      } else {
        name = this.queryData.name
      }
      this.loading = true
      getChangeList({
        data: {
          pageSize: this.pageSize,
          pageNum: this.pageNum,
          name
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.poList
          this.total = res.data.data.poSum
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* 查看问题点页面退出 */
    signOut (addActive) {
      this.addActive = addActive
    },
    /* 打开新增对比检测弹框 */
    openAddModal () {
      this.$refs.addForm.resetFields()
      this.addForm = {}
      this.modal1 = true
    },
    /* 保存添加新的人类活动变化检测任务 */
    submitAddForm () {
      this.$Spin.show()
      this.$refs.addForm.validate(valid => {
        if (valid) {
          addTask({
            data: {
              name: this.addForm.name,
              image1Id: this.addForm.image1Id,
              image2Id: this.addForm.image2Id,
              remark: this.addForm.remark
            }
          }).then(res => {
            this.$Spin.hide()
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '人类活动变化检测任务添加成功' })
              // 清空--添加新的人类活动变化检测任务--表单及校验
              this.$refs.addForm.resetFields()
              this.addForm = {}
              this.modal1 = false
              this.getTableList()
            } else {
              this.$Notice.error({ title: '人类活动变化检测任务添加失败' })
            }
          })
        }
      })
    },
    /* 删除人类活动信息比对任务 */
    proDelete (id) {
      delActivityChangeRask({
        data: {
          id
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '人类活动比对任务删除成功' })
          this.getTableList()
        } else {
          this.$Notice.error({ title: '人类活动比对任务删除失败', desc: res.data.msg })
        }
      })
    },
    /* 重新计算 */
    newcom (params) {
      this.$Spin.show()
      addTask({
        data: {
          name: params.row.name,
          image1Id: params.row.image1_id,
          image2Id: params.row.image2_id,
          remark: params.row.remark,
          id: params.row.id
        }
      }).then(res => {
        this.$Spin.hide()
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '人类活动变化检测任务重新计算成功' })
        } else {
          this.$Notice.error({ title: '人类活动变化检测任务重新计算失败' })
        }
      }).catch(() => {
        this.$Spin.hide()
      })
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .iconfont {
      margin-right: 4px;
    }
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .changeDetectTableMap {
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
  }
</style>
